.inputGroup {
  display: flex;
  flex-direction: column;
  max-width: 320px;
  margin: 0 0 12px 0;
}

.fieldLabel {
  color: #151515;
  padding: 6px 0;
  margin-right: 10px;
}

.fieldInput {
  border: 1px solid #c8c8c8;
  border-radius: 6px;
  height: 47px;
  padding: 8px 5px 7px;
  margin: 0;
  font-size: 14px;
}

.fieldCheckbox {
  appearance: none;
  min-height: 1.48em;
  min-width: 1.48em;
  margin-top: -2px;
  background: #fff;
  border: 1px solid #d5d5d5;
  box-shadow: inset 0 0 3px rgb(46 46 46 / 5%);
  cursor: pointer;
  transition: border-color .1s ease;
  border-radius: 6px;
  margin: -4px 6px 0 0;
  padding: 0;
  vertical-align: middle;
}

.fieldCheckbox:checked {
  background: #3e77aa center no-repeat url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
  background-size: 55%;
  background-origin: border-box;
  box-shadow: none;
}